'use client';

import React, { useEffect, useMemo, useState } from 'react';
import { useParams, useRouter } from 'next/navigation';

import Icon from '../../../../../components/Icon';
import { Topbar } from '../../../../../components/Topbar';
import { Button } from '../../../../../components/Button';
import { Card } from '../../../../../components/Card';

import { createClient } from '../../../../../lib/supabaseBrowser';

type WorkoutTypeUI = 'rodagem' | 'progressivo' | 'alternado';
type IntensityUI = 'leve' | 'moderado' | 'forte';

type StudentRow = {
  id: string;
  name: string;
  email: string | null;
  p1k_sec_per_km: number | null;
};

type BlockUI = {
  id: string;
  distanceKm: string; // string para não travar input (permitir apagar)
  intensity: IntensityUI;
};

function makeId() {
  return `${Date.now()}-${Math.random().toString(16).slice(2)}`;
}

function sanitizeKmInput(raw: string) {
  // permite: "", "1", "1.5", "1,5"
  const v = raw.replace(',', '.').replace(/[^0-9.]/g, '');
  // garante só 1 ponto
  const parts = v.split('.');
  if (parts.length <= 2) return v;
  return `${parts[0]}.${parts.slice(1).join('')}`;
}

function parseKm(v: string) {
  const n = parseFloat(v.replace(',', '.'));
  return Number.isFinite(n) ? n : 0;
}

function formatKm(n: number) {
  if (!Number.isFinite(n)) return '0';
  // 1 casa quando tem decimal
  const isInt = Math.abs(n - Math.round(n)) < 1e-9;
  return isInt ? String(Math.round(n)) : n.toFixed(1);
}

function formatPaceFromSec(sec?: number | null) {
  if (!sec || sec <= 0) return '—';
  const m = Math.floor(sec / 60);
  const s = Math.round(sec % 60);
  return `${m}:${String(s).padStart(2, '0')}`;
}

function paceRangeFromP1k(p1kSec: number | null, intensity: IntensityUI) {
  if (!p1kSec || p1kSec <= 0) return null;

  // heurística simples (ajuste fino depois com o Diego)
  const mult =
    intensity === 'leve'
      ? [1.2, 1.35]
      : intensity === 'moderado'
      ? [1.05, 1.15]
      : [0.9, 1.0];

  const a = Math.round(p1kSec * mult[0]);
  const b = Math.round(p1kSec * mult[1]);
  return [a, b] as const;
}

export default function NewWorkoutPage() {
  const router = useRouter();
  const params = useParams();

  const studentId = useMemo(() => {
    const raw = (params as any)?.id;
    return Array.isArray(raw) ? raw[0] : raw;
  }, [params]);

  const supabase = useMemo(() => createClient(), []);

  const [busy, setBusy] = useState(false);
  const [bannerError, setBannerError] = useState<string | null>(null);
  const [toast, setToast] = useState<string | null>(null);

  const [studentLoading, setStudentLoading] = useState(true);
  const [student, setStudent] = useState<StudentRow | null>(null);

  const [workoutType, setWorkoutType] = useState<WorkoutTypeUI>('rodagem');
  const [title, setTitle] = useState('');

  const [includeWarmup, setIncludeWarmup] = useState(true);
  const [warmupKm, setWarmupKm] = useState('2');

  const [includeCooldown, setIncludeCooldown] = useState(false);
  const [cooldownKm, setCooldownKm] = useState('1');

  const [blocks, setBlocks] = useState<BlockUI[]>([
    { id: makeId(), distanceKm: '5', intensity: 'moderado' },
  ]);

  const totalKm = useMemo(() => {
    const w = includeWarmup ? parseKm(warmupKm) : 0;
    const c = includeCooldown ? parseKm(cooldownKm) : 0;
    const b = blocks.reduce((sum, blk) => sum + parseKm(blk.distanceKm), 0);
    return Math.max(0, w + b + c);
  }, [includeWarmup, warmupKm, includeCooldown, cooldownKm, blocks]);

  const titlePlaceholder = useMemo(() => {
    if (workoutType === 'rodagem') return 'Ex: Rodagem leve';
    if (workoutType === 'progressivo') return 'Ex: Progressivo';
    return 'Ex: Alternado (ex.: 6x400m)';
  }, [workoutType]);

  // carrega aluno (sem .single() para não dar 406 quando RLS/session falhar)
  useEffect(() => {
    let alive = true;

    async function load() {
      setStudentLoading(true);
      setBannerError(null);

      try {
        const { data: userResp, error: userErr } = await supabase.auth.getUser();
        if (userErr) throw userErr;

        const user = userResp?.user;
        if (!user) {
          // se não estiver logado, manda pra login (não quebra o app)
          if (alive) {
            setBannerError('Você precisa estar logado para criar um treino.');
            setStudent(null);
          }
          return;
        }

        const { data, error } = await supabase
          .from('students')
          .select('id,name,email,p1k_sec_per_km')
          .eq('id', studentId)
          .maybeSingle();

        if (error) throw error;

        if (!data) {
          if (alive) {
            setBannerError('Aluno não encontrado (ou sem permissão para acessar).');
            setStudent(null);
          }
          return;
        }

        if (alive) setStudent(data as StudentRow);
      } catch (e: any) {
        if (alive) {
          setStudent(null);
          setBannerError(e?.message || 'Erro ao carregar aluno.');
        }
      } finally {
        if (alive) setStudentLoading(false);
      }
    }

    if (studentId) load();

    return () => {
      alive = false;
    };
  }, [supabase, studentId]);

  function setBlockDistance(id: string, value: string) {
    const v = sanitizeKmInput(value);
    setBlocks((prev) => prev.map((b) => (b.id === id ? { ...b, distanceKm: v } : b)));
  }

  function setBlockIntensity(id: string, intensity: IntensityUI) {
    setBlocks((prev) => prev.map((b) => (b.id === id ? { ...b, intensity } : b)));
  }

  function addBlock() {
    setBlocks((prev) => [...prev, { id: makeId(), distanceKm: '1', intensity: 'moderado' }]);
  }

  function removeBlock(id: string) {
    setBlocks((prev) => prev.filter((b) => b.id !== id));
  }

  function validateBeforeSave() {
    if (!studentId) return 'Aluno inválido.';
    if (!blocks.length) return 'Adicione ao menos 1 bloco principal.';

    const invalid = blocks.some((b) => parseKm(b.distanceKm) <= 0);
    if (invalid) return 'Todos os blocos devem ter distância maior que 0.';

    if (includeWarmup && parseKm(warmupKm) <= 0) return 'A distância do aquecimento deve ser maior que 0.';
    if (includeCooldown && parseKm(cooldownKm) <= 0) return 'A distância do desaquecimento deve ser maior que 0.';

    return null;
  }

  async function handleSaveDraft() {
    setToast(null);
    setBannerError(null);

    const v = validateBeforeSave();
    if (v) {
      setBannerError(v);
      return;
    }

    setBusy(true);
    try {
      const { data: userResp, error: userErr } = await supabase.auth.getUser();
      if (userErr) throw userErr;

      const user = userResp?.user;
      if (!user) {
        setBannerError('Sessão expirada. Faça login novamente.');
        router.push('/login');
        return;
      }

      const payload = {
        student_id: studentId,
        trainer_id: user.id,
        status: 'draft',
        template_type: workoutType, // evita violar o check constraint (usa PT-BR)
        include_warmup: includeWarmup,
        warmup_km: includeWarmup ? parseKm(warmupKm) : 0,
        include_cooldown: includeCooldown,
        cooldown_km: includeCooldown ? parseKm(cooldownKm) : 0,
        blocks: blocks.map((b) => ({
          distance_km: parseKm(b.distanceKm),
          intensity: b.intensity,
        })),
        total_km: totalKm,
        template_params: {
          title: title.trim() || null,
          ui_version: 1,
        },
        version: 1,
      };

      const { error } = await supabase.from('workouts').insert(payload);
      if (error) throw error;

      setToast('Rascunho salvo com sucesso.');
    } catch (e: any) {
      setBannerError(e?.message || 'Erro ao salvar rascunho.');
    } finally {
      setBusy(false);
    }
  }

  async function handleShare() {
    // no MVP: primeiro salva rascunho, depois melhora compartilhamento (P1)
    await handleSaveDraft();
    if (!bannerError) {
      setToast('Rascunho salvo. (Compartilhamento será refinado em seguida.)');
    }
  }

  return (
    <div className="min-h-screen bg-background-light dark:bg-background-dark">
      <Topbar title="Criar Treino" showBack />

      <div className="mx-auto max-w-xl px-4 pb-28 pt-6">
        {bannerError ? (
          <div className="mb-4 rounded-xl border border-red-500/30 bg-red-500/10 px-4 py-3 text-sm text-red-200">
            {bannerError}
          </div>
        ) : null}

        {toast ? (
          <div className="mb-4 rounded-xl border border-emerald-500/30 bg-emerald-500/10 px-4 py-3 text-sm text-emerald-200">
            {toast}
          </div>
        ) : null}

        <Card>
          <div className="px-5 py-4">
            <div className="text-xs tracking-widest text-muted-foreground">ALUNO</div>

            <div className="mt-2">
              <div className="text-lg font-semibold">
                {studentLoading ? 'Carregando...' : student?.name || '—'}
              </div>

              <div className="mt-1 text-sm text-muted-foreground">
                Ritmo P1K: {formatPaceFromSec(student?.p1k_sec_per_km)} min/km
              </div>
            </div>
          </div>
        </Card>

        <div className="mt-4" />

        <Card>
          <div className="px-5 py-4">
            <div className="text-base font-semibold">Título do treino</div>

            <input
              className="mt-3 w-full rounded-xl bg-black/20 px-4 py-3 text-base outline-none ring-1 ring-white/10 focus:ring-2 focus:ring-emerald-400/60"
              placeholder={titlePlaceholder}
              value={title}
              onChange={(e) => setTitle(e.target.value)}
            />

            <div className="mt-2 text-sm text-muted-foreground">
              Distância total estimada: {formatKm(totalKm)} km
            </div>
          </div>
        </Card>

        <div className="mt-4" />

        <Card>
          <div className="px-5 py-4">
            <div className="text-base font-semibold">Tipo de Treino</div>

            <div className="mt-3 flex gap-2">
              {(['rodagem', 'progressivo', 'alternado'] as WorkoutTypeUI[]).map((t) => (
                <button
                  key={t}
                  type="button"
                  onClick={() => setWorkoutType(t)}
                  className={[
                    'rounded-full px-4 py-2 text-sm font-semibold transition',
                    workoutType === t
                      ? 'bg-emerald-400 text-black'
                      : 'bg-black/20 text-white ring-1 ring-white/10 hover:bg-black/30',
                  ].join(' ')}
                >
                  {t === 'rodagem' ? 'Rodagem' : t === 'progressivo' ? 'Progressivo' : 'Alternado'}
                </button>
              ))}
            </div>
          </div>
        </Card>

        <div className="mt-4" />

        <Card>
          <div className="px-5 py-4">
            <div className="text-base font-semibold">Estrutura</div>

            {/* Aquecimento */}
            <div className="mt-4 rounded-2xl bg-black/10 px-4 py-4 ring-1 ring-white/10">
              <div className="flex items-center justify-between">
                <div className="flex items-center gap-2 font-semibold">
                  <Icon name="flame" />
                  Aquecimento
                </div>

                <button
                  type="button"
                  onClick={() => setIncludeWarmup((v) => !v)}
                  className={[
                    'h-7 w-12 rounded-full p-1 transition',
                    includeWarmup ? 'bg-emerald-400' : 'bg-black/30 ring-1 ring-white/10',
                  ].join(' ')}
                  aria-checked={includeWarmup}
                  role="switch"
                >
                  <div
                    className={[
                      'h-5 w-5 rounded-full bg-white transition',
                      includeWarmup ? 'translate-x-5' : 'translate-x-0',
                    ].join(' ')}
                  />
                </button>
              </div>

              <div className="mt-4 grid grid-cols-3 gap-3">
                <div className="col-span-2">
                  <div className="text-xs tracking-widest text-muted-foreground">DISTÂNCIA</div>
                  <div className="mt-2 flex items-center gap-2">
                    <input
                      disabled={!includeWarmup}
                      inputMode="decimal"
                      className="w-full rounded-xl bg-black/20 px-4 py-3 text-base outline-none ring-1 ring-white/10 focus:ring-2 focus:ring-emerald-400/60 disabled:opacity-40"
                      value={warmupKm}
                      onChange={(e) => setWarmupKm(sanitizeKmInput(e.target.value))}
                    />
                    <div className="text-sm text-muted-foreground">km</div>
                  </div>
                </div>

                <div>
                  <div className="text-xs tracking-widest text-muted-foreground">RITMO</div>
                  <div className="mt-2 rounded-xl bg-black/20 px-4 py-3 text-base ring-1 ring-white/10">
                    Livre
                  </div>
                </div>
              </div>
            </div>

            {/* Blocos Principais */}
            <div className="mt-5">
              <div className="text-xs tracking-widest text-muted-foreground">BLOCOS PRINCIPAIS</div>

              <div className="mt-3 space-y-3">
                {blocks.map((blk) => {
                  const range = paceRangeFromP1k(student?.p1k_sec_per_km ?? null, blk.intensity);

                  return (
                    <div key={blk.id} className="rounded-2xl bg-black/10 px-4 py-4 ring-1 ring-white/10">
                      <div className="flex items-start justify-between gap-3">
                        <div className="flex-1">
                          <div className="text-xs tracking-widest text-muted-foreground">
                            DISTÂNCIA DO TRECHO
                          </div>

                          <div className="mt-2 flex items-center gap-2">
                            <input
                              inputMode="decimal"
                              className="w-full rounded-xl bg-black/20 px-4 py-3 text-base outline-none ring-1 ring-white/10 focus:ring-2 focus:ring-emerald-400/60"
                              value={blk.distanceKm}
                              onChange={(e) => setBlockDistance(blk.id, e.target.value)}
                              placeholder="Ex: 1,0"
                            />
                            <div className="text-sm text-muted-foreground">km</div>
                          </div>
                        </div>

                        <button
                          type="button"
                          className="mt-1 rounded-xl bg-black/20 p-2 ring-1 ring-white/10 hover:bg-black/30"
                          onClick={() => removeBlock(blk.id)}
                          aria-label="Remover bloco"
                          title="Remover bloco"
                        >
                          <Icon name="trash" />
                        </button>
                      </div>

                      <div className="mt-4">
                        <div className="flex items-center justify-between">
                          <div className="text-xs tracking-widest text-muted-foreground">INTENSIDADE</div>
                          {range ? (
                            <div className="text-xs text-muted-foreground">
                              Ritmo sugerido: {formatPaceFromSec(range[0])}–{formatPaceFromSec(range[1])} /km
                            </div>
                          ) : null}
                        </div>

                        <div className="mt-3 grid grid-cols-3 gap-2">
                          {(['leve', 'moderado', 'forte'] as IntensityUI[]).map((i) => (
                            <button
                              key={i}
                              type="button"
                              onClick={() => setBlockIntensity(blk.id, i)}
                              className={[
                                'rounded-full px-3 py-2 text-sm font-semibold transition',
                                blk.intensity === i
                                  ? 'bg-emerald-400 text-black'
                                  : 'bg-black/20 text-white ring-1 ring-white/10 hover:bg-black/30',
                              ].join(' ')}
                            >
                              {i === 'leve' ? 'Leve' : i === 'moderado' ? 'Moderado' : 'Forte'}
                            </button>
                          ))}
                        </div>
                      </div>
                    </div>
                  );
                })}

                <button
                  type="button"
                  onClick={addBlock}
                  className="w-full rounded-2xl border border-dashed border-white/20 bg-transparent px-4 py-4 text-center text-sm font-semibold text-white/80 hover:bg-white/5"
                >
                  + Adicionar Bloco
                </button>
              </div>
            </div>

            {/* Desaquecimento */}
            <div className="mt-5 rounded-2xl bg-black/10 px-4 py-4 ring-1 ring-white/10">
              <div className="flex items-center justify-between">
                <div className="font-semibold">Desaquecimento</div>

                <button
                  type="button"
                  onClick={() => setIncludeCooldown((v) => !v)}
                  className={[
                    'h-7 w-12 rounded-full p-1 transition',
                    includeCooldown ? 'bg-emerald-400' : 'bg-black/30 ring-1 ring-white/10',
                  ].join(' ')}
                  aria-checked={includeCooldown}
                  role="switch"
                >
                  <div
                    className={[
                      'h-5 w-5 rounded-full bg-white transition',
                      includeCooldown ? 'translate-x-5' : 'translate-x-0',
                    ].join(' ')}
                  />
                </button>
              </div>

              <div className="mt-4 grid grid-cols-3 gap-3">
                <div className="col-span-2">
                  <div className="text-xs tracking-widest text-muted-foreground">DISTÂNCIA</div>
                  <div className="mt-2 flex items-center gap-2">
                    <input
                      disabled={!includeCooldown}
                      inputMode="decimal"
                      className="w-full rounded-xl bg-black/20 px-4 py-3 text-base outline-none ring-1 ring-white/10 focus:ring-2 focus:ring-emerald-400/60 disabled:opacity-40"
                      value={cooldownKm}
                      onChange={(e) => setCooldownKm(sanitizeKmInput(e.target.value))}
                    />
                    <div className="text-sm text-muted-foreground">km</div>
                  </div>
                </div>

                <div>
                  <div className="text-xs tracking-widest text-muted-foreground">RITMO</div>
                  <div className="mt-2 rounded-xl bg-black/20 px-4 py-3 text-base ring-1 ring-white/10">
                    Livre
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Card>
      </div>

      {/* Footer fixo (móvel) */}
      <div className="fixed bottom-0 left-0 right-0 bg-background-light/80 dark:bg-background-dark/80 backdrop-blur">
        <div className="mx-auto flex max-w-xl gap-3 px-4 py-4">
          <Button variant="secondary" fullWidth onClick={handleSaveDraft} disabled={busy}>
            {busy ? 'Salvando...' : 'Salvar Rascunho'}
          </Button>

          <Button fullWidth onClick={handleShare} disabled={busy}>
            Compartilhar
          </Button>
        </div>
      </div>
    </div>
  );
}
